import {
    createRouter,
    createWebHashHistory
} from 'vue-router'

const E404 = ()=> import('./../views/404.vue')
// const User = ()=> import('./../views/user.vue')

const routes = [{
        path: '/404',
        name: '404',
        component: E404
    }
]
export default createRouter({
    history: createWebHashHistory(),
    routes
});